import tokenRequest from '@/utils/tokenRequest'

// 获取用户个人信息
export function getUserInfo () {
  return tokenRequest.get('/user/info')
}

// 获取优惠券等信息
export function getUserAssets () {
  return tokenRequest.get('/user/assets')
}

// 更换头像
export function uploadImage (obj) {
  return tokenRequest.post('/upload/image', obj)
}
