<template>
    <article>
      <header>
        <div class="box" v-if="userInfo.nick_name">
          <div class="left">
            <input type="file" id="file" @change="postImage">
            <label for="file">
              <img src="@/assets/defaultImage.webp" alt="">
            </label>
          </div>
          <div class="right">
            <div>
              <h3>{{userInfo.nick_name}}</h3>
              <p>{{userInfo.mobile}}</p>
            </div>
          </div>
        </div>
        <div class="box" v-else  @click="$router.replace('/login')">
          <div class="left">
            <img src="@/assets/defaultImage.webp" alt="">
          </div>
          <div class="right">
            <div>
              <h3>未登录</h3>
              <p>点击登录</p>
            </div>
          </div>
        </div>
      </header>
      <main>
        <div class="account">
          <div class="unfilled">
            <span v-if="userInfo.mobile">{{userInfo.balance}}</span>
            <span v-else>- -</span>
            <p>账户余额</p>
          </div>
          <div class="commom">
            <span v-if="userInfo.mobile">{{assets.points}}</span>
            <span v-else>- -</span>
            <p>积分</p>
          </div>
          <div class="commom">
            <span v-if="userInfo.mobile">{{assets.coupon}}</span>
            <span v-else>- -</span>
            <p>优惠券</p>
          </div>
          <div class="mywallit">
            <van-icon name="pending-payment" />
            <p>我的钱包</p>
          </div>
        </div>
        <div class="order">
          <div @click="$router.push('/myorder')">
            <van-icon v-if="counts.refund" name="description-o" :badge="counts.refund"/>
            <van-icon v-else name="description-o"/>
            <p>全部订单</p>
          </div>
          <div @click="$router.push('/myorder?dataType=payment')">
            <van-icon v-if="counts.payment" name="peer-pay" :badge="counts.payment"/>
            <van-icon v-else name="peer-pay"/>
            <p>待支付</p>
          </div>
          <div @click="$router.push('/myorder?dataType=delivery')">
            <van-icon v-if="counts.delivery" name="logistics" :badge="counts.delivery" />
            <van-icon v-else name="logistics" />
            <p>代发货</p>
          </div>
          <div @click="$router.push('/myorder?dataType=received')">
            <van-icon v-if="counts.received" name="sign" :badge="counts.received"/>
            <van-icon v-else name="sign"/>
            <p>待收货</p>
          </div>
        </div>
        <div class="serve">
          <h3>我的服务</h3>
          <div class="bottom">
            <ul>
              <li @click="$router.push('/adresslist')">
                <van-icon name="location-o" />
                <p>收获地址</p>
              </li>
              <li>
                <van-icon name="point-gift-o" />
                <p>领券中心</p>
              </li>
              <li>
                <van-icon name="cash-o" />
                <p>优惠券</p>
              </li>
              <li>
                <van-icon name="question-o" />
                <p>我的帮助</p>
              </li>
              <li>
                <van-icon name="gold-coin-o" />
                <p>我的积分</p>
              </li>
              <li>
                <van-icon name="balance-pay" />
                <p>退换/售后</p>
              </li>
            </ul>
          </div>
        </div>
      </main>
      <footer>
        <button @click="clearLogin">退出登录</button>
      </footer>
    </article>
</template>

<script>
import { getUserInfo, getUserAssets, uploadImage } from '@/api/user'
import { getOrderToDoCounts } from '@/api/order'
import { Toast } from 'vant'
import loginConfirm from '@/mixins/loginConfirm'
export default {
  name: 'myIndex',
  mixins: [loginConfirm],
  data () {
    return {
      userInfo: {},
      assets: {},
      counts: {}
    }
  },
  methods: {
    async getUserInfoAction () {
      const { data: { userInfo } } = await getUserInfo()
      this.userInfo = userInfo
      console.log(userInfo)
    },
    async getUserAssetsAction () {
      const { data: { assets } } = await getUserAssets()
      this.assets = assets
    },
    async getgetOrderToDoCountsAction () {
      const { data: { counts } } = await getOrderToDoCounts()
      this.counts = counts
    },
    async postImage (e) {
      const file = e.target.files[0]
      const formData = new FormData()
      formData.append('file', file)
      const res = await uploadImage(formData)
      console.log(res)
      Toast(res.message)
    },
    clearLogin () {
      if (!this.$store.state.user.userInfo) return

      this.$dialog.confirm({
        title: '温馨提示',
        message: '您确定要退出吗',
        confirmButtonText: '确认',
        confirmButtonColor: 'red',
        cancelButtonText: '取消'
      })
        .then(() => {
          this.$store.dispatch('user/clearLogin')
        })
        .catch(() => {

        })
    }
  },

  created () {
    if (!this.$store.state.user.userInfo) return
    this.getUserInfoAction()
    this.getUserAssetsAction()
    this.getgetOrderToDoCountsAction()
  }
}
</script>

<style lang="less" scoped>
article {
  overflow: auto;
  background-color: #fafafa;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 50px;
  header {
    padding: 0 15px;
    height: 130px;
    display: flex;
    align-items: center;
    background: #faf5ed;
    .box {
      display: flex;
      height: 60px;
      .left {
        margin-right: 20px;
        width: 60px;
        border-radius: 50%;
        overflow: hidden;
        #file {
          display: none;
        }
        img {
          width: 100%;
        }
      }
      .right {
        display: flex;
        align-items: center;
        > div {
          height: 80%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          h3 {
            font-size: 19px;
            color: #c9aa90;
          }
          p {
            font-size: 16px;
            color: #e8c473;
          }
        }
      }
    }
  }
  main {
    .account {
      background: #fff;
      padding: 15px 0;
      height: 105px;
      display: flex;
      text-align: center;
      font-size: 14px;
      span {
        color: red;
        font-size: 18px;
      }
      p {
        margin-top: 8px;
      }
      .unfilled {
        width: 33%;
      }
      .commom {
      flex: 1;
      }
      .mywallit {
        width: 25%;
        border-left: 1px solid #ececec;
        p {
          margin-top: 7px;
        }
        i {
          margin-top: -2px;
          font-size: 22px;
        }
      }
    }
    .order {
      border-radius: 5px;
      background: #fff;
      height: 80px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      margin: 10px;
      padding-top: 10px;
      > div {
        text-align: center;
        i {
          font-size: 22px;

        }
        p {
          font-size: 14px;
          margin-top: 5px;
        }
      }
    }
    .serve {
      border-radius: 5px;
      background:#fff;
      margin: 0 10px;
      h3 {
        font-size: 15px;
        height: 45px;
        line-height: 45px;
        padding-left: 14px;
        font-weight: 400;
      }
      .bottom {
        padding: 10px 0 0 0 ;
      }
      ul {
        height: 140px;
        display: flex;
        flex-flow: wrap;
        li {
          width: 25%;
          text-align: center;
          i {
            font-size: 22px;
            color: red;
          }
          p {
            margin-top: 8px;
            font-size: 14px;
          }
        }
      }
    }
  }
  footer {
    height: 85.5px;
    display: flex;
    justify-content: center;
    align-items: center;
    button {
      width: 60%;
      height: 40%;
      border: 1px solid lightgray;
      background: transparent;
      border-radius: 10px;
      font-size: 14px;
    }
  }
}
</style>
